<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="模板录入" name="templete">
      <el-row>
        <el-col :span="8" class="upload">
          <el-button class="downloadExcel"><a href="http://www.goldlone.cn/sc/stu/exportModel
">点击下载模板</a></el-button>
          <el-upload
            class="upload-demo"
            action="http://www.goldlone.cn/sc/stu/add"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :before-remove="beforeRemove"
            multiple
            :limit="3"
            :on-exceed="handleExceed"
            :file-list="fileList">
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能按照我们指定的模板上传</div>
          </el-upload>
        </el-col>
      </el-row>
    </el-tab-pane>
    <el-tab-pane label="单独录入" name="single">
      
    </el-tab-pane>
  </el-tabs>
</template>

<script>
    let moment = require("moment");
    export default {
        name: "insert-student",
        data(){
          return{
            fileList:[],
            activeName:"templete"
          }
        },
      methods: {
        handleRemove(file, fileList) {
          console.log(file, fileList);
        },
        handlePreview(file) {
          console.log(file);
        },
        handleExceed(files, fileList) {
          this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
        },
        beforeRemove(file, fileList) {
          return this.$confirm(`确定移除 ${ file.name }？`);
        }
      }
    }
</script>

<style scoped>
  .upload{
    margin-top: 20px;
  }
  .downloadExcel a{
    text-decoration: none;
    color:black;
  }
  .downloadExcel{
    margin-bottom: 20px;
  }
</style>



